<template>
  <el-container id="app">
    <el-header id="header" height="50px">
      <Header/>
    </el-header>
    <div style="padding:0;" >
      <div class="top-banner"></div>
      <div class='tit-line'>
        <span class="tit" style="font-size: 38px;">登录</span>
      </div>
      <div class="login-box">
        <div class="login-left">
          <div class="qrCodeBox">
            <img width="180px" src="data:image/png;base64," alt="登录">
            <div class="qrcode-footer">
              <p class="status-txt">扫描二维码登录</p>
             <p class="app-link">请使用<a href="javascript:;" >客户端</a><br>扫码登录<br>或扫码下载APP</p>
            </div>
          </div>
        </div>
        <div class="login-right">
          <el-tabs v-model="activeName" class="tabs" >
            <el-tab-pane label="密码登录" name="first" style="margin-left:-30px">
              <el-form :model="ruleForm1" :rules="rules" ref="ruleForm1" label-width="100px" >
                <el-form-item label="名称" prop="name">
                  <el-input v-model="ruleForm1.name" placeholder="请输入用户名"></el-input>
                </el-form-item>
                <el-form-item label="密码" prop="password">
                  <el-input type="password" v-model="ruleForm1.password" autocomplete="off" placeholder="请输入密码"></el-input>
                </el-form-item>
                <el-form-item>
                  <el-button type="primary" @click="submitForm('ruleForm1')">登录</el-button>
                  <el-button @click="resetForm('ruleForm1')">重置</el-button>
                  <el-link type="primary" style="margin-left:10px;font-size:12px">忘记密码？</el-link>
                </el-form-item>
              </el-form>
            </el-tab-pane>
            <el-tab-pane label="短信登录" name="second"  style="margin-left:-30px">
              <el-form :model="ruleForm2" :rules="rules" ref="ruleForm2" label-width="100px" >
                <el-form-item label="电话" prop="phone">
                  <el-input v-model="ruleForm2.phone" placeholder="请输入电话"></el-input>
                </el-form-item>
                <el-form-item label="验证码" prop="validCode" style="position:relative">
                  <el-input  v-model="ruleForm2.validCode" autocomplete="off" placeholder="请输入验证码"></el-input>
                  <el-button 
                  :disabled="!isTimeOver"
                  @click="toggleTime"
                  size="small" 
                  type="primary" 
                  class="send-code" 
                  round>{{isTimeOver?"点击发送验证码":`${time}秒后再次点击发送`}}</el-button>
                </el-form-item>
                <el-form-item>
                  <el-button type="primary" @click="submitForm('ruleForm2')">登录</el-button>
                  <el-button @click="resetForm('ruleForm2')">重置</el-button>
                  <el-link type="primary" style="margin-left:10px;font-size:12px">忘记密码？</el-link>
                </el-form-item>
              </el-form>
            </el-tab-pane>
          </el-tabs>
        </div>
      </div>
    </div>
      <Footer/>
  </el-container>
</template>

<script>
import Header from '../../components/Header'
import Footer from '../../components/Footer'
export default {
  name:'login',
    data () {
      return {
          time:30,
          isTimeOver:true,
          activeName:'first',
          ruleForm1: {
            name: '',
            password:''
          },
          ruleForm2: {
            phone: '',
            validCode:''
          },
          rules: {
            name: [
              { required: true, message: '请输入名称', trigger: 'blur' },
              { min: 1, max: 12, message: '长度在 1 到 12个字符', trigger: 'blur' },
              { validator: this.validateName, trigger: 'blur' }
            ],
            password:[
              { required: true, message: '请输入密码', trigger: 'blur' },
              { min: 6, max: 12, message: '长度在 6 到 12个字符', trigger: 'blur' },
              { validator: this.validateName, trigger: 'blur' }
            ],
            phone: [
              { required: true, message: '请输入电话', trigger: 'blur' },
              { validator: this.validatePhone, trigger: 'blur' }
            ],
            validCode:[
              { required: true, message: '请输入验证码', trigger: 'blur' },
              { validator: this.validateCode, trigger: 'blur' }
            ]
          }
      }
    },
   components: {
     Header,Footer
   },
   methods: {
      toggleTime(){
        this.isTimeOver=false
        this.timer=setInterval(() => {
            this.time--
            if(this.time===0){
              clearInterval(this.timer)
              this.isTimeOver=true
              this.time=30
            }
        }, 1000);
      },
      validatePhone(rule,value,callback){
        if(!(/^1\d{10}$/.test(value))){
          callback(new Error('格式错误'))
        }else{
          callback()
        }
      },
      validateCode(rule,value,callback){
        if(!(/^\d{6}$/.test(value))){
          callback(new Error('验证码错误'))
        }else{
          callback()
        }
      },
      validateName(rule,value,callback){
        if(!(/^\w+$/g.test(value))){
          callback(new Error('格式错误'))
        }else{
          callback()
        }
      },
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.$message({
              message: '登录成功',
              type: 'success'
            })
            window.localStorage.setItem('user',JSON.stringify({name:this.ruleForm1.name}))
            this.$router.push('/home')
          } else {
            return false
          }
        })
      },
      resetForm(formName) {
        this.$refs[formName].resetFields()
      }
   }
  
}
</script>

<style scoped lang="less">
  #app{
     #header{
      background-color: rgba(255,165,0,.5);
      box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.08);
     }
     .tit-line{
        width: 980px;
        height: 28px;
        margin: 0 auto;
        border-bottom: 1px solid rgba(0,0,0,.2);
        margin-bottom: 28px;
        text-align: center;
        .tit{
          height: 56px;
          line-height: 56px;
          margin: 0 auto;
          padding: 0 20px;
          font-size: 40px;
          background: #fff;
          text-align: center;
        }
     }
     .top-banner{
      background: rgb(245,245,245);
      height: 10px;
      margin-bottom: 20px;
      box-shadow: 0 4px 6px 0 rgba(0, 0, 0, 0.08);
     }
      .login-box{
        &:extend(.clearfix);
        position: relative;
        width: 980px;
        margin: 0 auto;
        .login-left{
          float: left;
          width: 489px;
          position: relative;
          .qrCodeBox{
            position: absolute;
            left: 50%;
            transform: translateX(-50%);
            .qrcode-footer{
              height: 155px;
              .status-txt{
                  padding-top: 20px;
                  text-align: center;
                  color: #222;
                  font-size: 18px;
                  letter-spacing: 3px;
                  margin-bottom: 5px;
              }
              .app-link{
                text-align: center;
                color: #717171;
                font-size: 14px;
              }
            }
          }
        }
        .login-right{
            float: right;
            padding-left: 45px;
            width: 490px;
            .tabs{
              padding-left: 60px;
              border-left: 1px solid rgba(0,0,0,.2);
              .send-code{
                position: absolute;
                right:5px;
                top:50%;
                transform: translateY(-50%)
              }
            }
        }
      }
  }
</style>
 